<template>
    <div class="my-points-list">
        <template v-for="item in pointsList">
            <cell ref="pointCell" :title="item.status" :label="item.date" :value="item.amount" :desc="item.balance"></cell>
        </template>
    </div>
</template>

<script>
    export default {
        name: 'my-points-list',
        data() {
            return {
                pointsList: null
            };
        },
        created() {
            let ls = this.$ls.getItem('points-list');
            this.pointsList = ls;
            this.pointsList.filter(item => {
                if (item.status === 'in') {
                    item.status = '新增';
                    item.amount = '+' + item.amount;
                } else {
                    item.amount = '-' + item.amount;
                    item.status = '消费';
                }
                item.date = '积分来源 ' + item.date;
                item.balance = '积分余额： ' + item.balance;
            });
        },
        mounted() {
            let nodes = document.getElementsByClassName('value');
            for (let val of nodes) {
                let flag = val.innerHTML.substring(0, 1);
                val.style.color = flag === '+' ? '#ef7474' : '#4caf50';
            }
        }
    };

</script>

<style lang="scss" scoped>

</style>


