<template>
    <div class="my-points">
        <div class="hd">
            <div class="title">
                <p>我的积分</p>
                <img src="../../../assets/img/message.png" width="20" height="20">
            </div>
            <div class="value">
                {{points}}
            </div>
            <div class="desc">
                此积分为储蓄卡积分总数
            </div>
        </div>
        <div class="field-part">
            <cell-date-zone title="时间跨度" ref="dateZone" @date-zone-change=" dateZoneChange "></cell-date-zone>
            <mt-cell title="起始日期" :value=" dataForm.beginDate ">
            </mt-cell>
            <mt-cell title="结束日期" :value=" dataForm.endDate ">
            </mt-cell>
        </div>

        <div class="field-warn">
            仅支持查询时间跨三个月内的交易明细记录
        </div>

        <div class="field-action">
            <mt-button type="default" size="large" @click="query">查询</mt-button>
        </div>
    </div>
</template>

<script>
    import * as services from '@/services/my-data';
    export default {
        name: 'my-points',
        data() {
            return {
                points: '',
                dataForm: {
                    beginDate: '',
                    endDate: ''
                }
            };
        },
        created() {
            services.queryPoints()
                .then(res => {
                    this.points = res.data[0].points;
                });
        },
        mounted() {
            this.$refs.dateZone.setDefault(this.dataForm.beginDate, this.dataForm.endDate);
        },
        methods: {
            dateZoneChange(begin, end) {
                this.dataForm.beginDate = begin;
                this.dataForm.endDate = end;
            },
            query() {
                let params = this.dataForm;
                services.queryPointsList(params)
                    .then(res => {
                        this.$ls.setItem('points-list', res.data);
                        this.$router.push({ name: 'MyPointsList' });
                    });
            }
        }
    };

</script>

<style lang="scss" scoped>
    .hd {
        position: relative;
        margin-bottom: 15px;
        text-align: center;
        background-color: #fff;
        @include mini-border(bottom, #d9d9d9, before);

        .title {
            padding-top: 25px;
            font-size: 14px;

            p {
                display: inline-block;
                margin-top: 5px;
            }
            img {
                display: inline-block;
            }
        }

        div+div {
            margin-top: 25px;
        }

        .value {
            color: #ffa73f;
            font-size: 20px;
        }

        .desc {
            padding-bottom: 20px;
        }
    }
</style>


