<template>
    <div class="my-loan">
        <div class="hd" v-if="flag">
            <div class="hd-title">
                <p>我的贷款情况</p>
            </div>
            <div class="hd-wrap">
                <div class="left">
                    <p class="balance" v-text="loan.LOAN_BALANCE"></p>
                    <p>贷款余额（元）</p>
                </div>
                <div class="right">
                    <p class="total" v-text="loan.LOAN_AMOUNT"></p>
                    <p>贷款总额（元）</p>
                </div>
            </div>
        </div>
        <div class="field-action loan-null" v-else>
            <mt-button type="default" size="large">金农易贷</mt-button>
        </div>
        <!-- 贷款列表 -->
        <div class="main">
            <div @click="getDetail(item)" class="loan-list" v-for="item in loanList" v-if="loanList.length > 0">
                <mt-cell :title="item.LOAN_TYPE" is-link></mt-cell>
                <div class="hd-wrap">
                    <div class="left">
                        <p class="balance">{{item.LOAN_BALANCE}}</p>
                        <p>贷款余额（元）</p>
                    </div>
                    <div class="right">
                        <p class="total">{{item.LOAN_AMOUNT}}}</p>
                        <p>贷款总额（元）</p>
                    </div>
                </div>
            </div>
            <div class="data-null" v-else>
                您还未在我行办理贷款!
            </div>
        </div>
    </div>
</template>

<script>
    import * as services from '@/services/my-data';
    export default {
        name: 'my-loan',
        data() {
            return {
                loanList: null,
                loan: {
                    LOAN_AMOUNT: '',
                    LOAN_BALANCE: ''
                },
                flag: false
            };
        },
        created() {
            services.queryLoan()
                .then(res => {
                    if (res.data.length > 0) {
                        this.loan = res.data[0];
                        this.flag = true;
                    }
                });
            services.queryLoanList()
                .then(res => {
                    this.loanList = res.data;
                });
        },
        methods: {
            getDetail(item) {
                this.$ls.setItem('loan-info', item);
                this.$router.push({ name: 'MyLoanInfo' });
            }
        }
    };

</script>

<style lang="scss" scoped>
    .hd {
        padding: 20px;
        background-color: #777;
        color: #fff;

        p {
            color: #fff;
            font-size: 12px;
        }
    }

    .loan-null {
        background: #fff;
    }

    .data-null {
        margin: 30px 0;
        text-align: center;
        font-size: 16px;
    }

    .loan-list {
        position: relative;
        background-color: #fff;
        margin: 15px 0;
        padding-bottom: 26px;
        @include mini-border(bottom, #999, before);
    }

    .hd-wrap {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        margin-top: 20px;

        .left {
            flex: 1;
            padding: 20px 0;
            text-align: center;

            p+p {
                margin-top: 15px;
            }

            .balance {
                font-size: 18px;
            }
        }

        .right {
            position: relative;
            flex: 1;
            padding: 20px 0;
            text-align: center;
            @include mini-border(left, #ccc, before);

            p+p {
                margin-top: 15px;
            }

            .total {
                font-size: 18px;
            }
        }
    }
</style>


